import { observable,computed } from "mobx"

export interface IFilterChecker {
    checked: boolean;
    label: string;
    filters: string[];
}
export default class FilterChecker implements IFilterChecker {
    @observable
    checked: boolean;
    label: string;
    filters: string[];
    constructor(label: string, valid: boolean, filters: string[]) {
        this.label = label;
        this.checked = valid;
        this.filters = filters;
    }
}
export interface IColumnFilter {
    name: string;
    dbColName: string;
    arrTypes: IFilterChecker[];
    allTypesOption : number;
    isAllValsFalse: boolean;
}
export class ColumnFilter implements IColumnFilter {
    name: string;
    dbColName: string;
    @observable
    arrTypes: IFilterChecker[];
    allTypesOption : number;
    constructor(name: string, dbColName: string, allTypesOption: number = -1) {
        this.name = name;
        this.dbColName = dbColName;
        this.arrTypes = [];
        this.allTypesOption = allTypesOption;
    }
    @computed get isAllValsFalse () {
        const res = this.arrTypes.reduce(function(counter,currentValue){
            return currentValue.checked? ++counter:counter;
          },0);
        return (res ===0);
    }
    // @computed get countTrueVals () {
    //     const res = this.arrTypes.reduce(function(counter,currentValue){
    //         return currentValue.checked? ++counter:counter;
    //       },0);
    //     return res;
    // }
}

export const initInjurySeverity = () => {
    const col : IColumnFilter = new ColumnFilter('Severity','injury_severity_hebrew')
    col.arrTypes.push(new FilterChecker('dead', true, [""]));
    col.arrTypes.push(new FilterChecker('severly-injured', false, [" "]));
    return col;
}

